// Buttons
// Default button light
.button {
    text-decoration: none;
    color: $white;
    display: inline-block;
    text-transform: uppercase;
    font-size: 1.4rem;
    line-height: 1.6rem;
    border: 1px solid $white;
    padding: 2rem 1rem;
    text-align: center;
    max-width: 23.9rem;
    max-height: 65px;
    width: 100%;
    transition: ease 1s ;

    &:hover,
    &:active {
        color: $primary-cod;
        background-color: $white;
}
}

// Button Dark
.button--dark {
    background-color: $primary-cod;
    color: $white;
    border: 1px solid $white;

    &:hover {
        color: $primary-cod;
        background: $white;
        border: 1px solid $secondary-ebony;
    }
}